<template v-if="loaded==true">
  <div class="schedul2Title">
    &bull; 개인일정
  </div>
  <div class="schedul2Contents">
    <table class="PrivateTable">
      <template v-for="(item, i) in thisWeekDatas" v-bind:key="i">
        <tr>
          <td class="date">
            {{ item.evtDate }}
          </td>
          <td class="event">
            {{ item.data.SUMMARY }}
          </td>
        </tr>
      </template>
    </table>
  </div>
</template>

<script>
import axios from "axios";
// import ical2json from "ical2json"

export default {
  name: 'TimeCompo',
  components: {},
  props: {},
  data() {
    return {
      loaded: false,
      toWeek: "",
      startWeek: "",
      endWeek: "",
      thisWeekDatas: [],
    }
  },
  methods: {
    getThisWeek: function () {
      var value = [];
      var now = new Date();
      var nowDayOfWeek = now.getDay();
      var nowDay = now.getDate();
      var nowMonth = now.getMonth();
      var nowYear = now.getYear();
      nowYear += (nowYear < 2000) ? 1900 : 0;
      var weekStartDate = new Date(nowYear, nowMonth, nowDay - nowDayOfWeek);
      var weekEndDate = new Date(nowYear, nowMonth, nowDay + (6 - nowDayOfWeek));

      value.push(weekStartDate);
      value.push(weekEndDate);

      return value;
    },
    isThisWeek: function (evtDate) {
      var cDate = evtDate.setHours(0, 0, 0, 0)
      var nowDate = new Date()
      nowDate.setHours(0, 0, 0, 0)
      if (nowDate <= cDate && this.toWeek[1] >= cDate) {
        return true
      }
      return false;
    },
    getDateFormat: function (evtDate) {
      if( typeof(evtDate) != "string" ) return "";

      return evtDate.substring(0, 4) + "-" + evtDate.substring(4, 6) + "-" + evtDate.substring(6, 8);
    }
  },
  async created() {
    this.toWeek = this.getThisWeek()
    this.startWeek = this.toWeek[0].getFullYear() + "." + (this.toWeek[0].getMonth() + 1) + "." + this.toWeek[0].getDate()
    this.endWeek = this.toWeek[1].getFullYear() + "." + (this.toWeek[1].getMonth() + 1) + "." + this.toWeek[1].getDate()

    axios.get("/ical2", {
      headers: {
        "Content-Type": "application/xml",
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept"
      },
    })
        .then(response => {
          const any = require("ical2json");

          var reData = response.data.replaceAll("DTSTART;VALUE=DATE", "DTSTART").replaceAll("DTEND;VALUE=DATE", "DTEND")
          var icalJson = any.convert(reData);
          for (var i = 0; i < 50; i++) {
            if (this.isThisWeek(new Date(this.getDateFormat(icalJson.VCALENDAR[0].VEVENT[i].DTSTART))) == false) {
              continue;
            } else {
              // console.log(icalJson.VCALENDAR[0].VEVENT[i])
              this.thisWeekDatas.push({
                evtDate: this.getDateFormat(icalJson.VCALENDAR[0].VEVENT[i].DTSTART),
                data: icalJson.VCALENDAR[0].VEVENT[i]
              })
            }
          }

          function compare(a, b) {
            if (a.evtDate < b.evtDate)
              return -1;
            if (a.evtDate > b.evtDate)
              return 1;
            return 0;
          }

          this.thisWeekDatas.sort(compare)
        });


    this.loaded = true;
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.schedul2Title {
  font-size: 17px;

  margin-left: 10px;
  text-align: left;
  /*border-bottom: 3px #888;*/
  /*border-bottom-style: double ;*/
  /*width: 100px;*/
}

.schedul2Contents {
}

.schedul2Contents .PrivateTable {
  margin-top: 15px;
  width: 100%;
}

.schedul2Contents .PrivateTable .date {
}

.schedul2Contents .PrivateTable .event {
  text-align: left;
}

</style>
