<template>
  <div class="footer">
    <marquee scrollamount="10"> {{headline}}</marquee>
  </div>
</template>

<script>
import axios from "axios";
import xmlParser from "fast-xml-parser";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Footer',
  components: {
  },
  props: {
    msg: String
  },
  data(){
    return{
      news : [        "/flash",        "/etnews",        "/economy",        "/politics",        "/international",        "/sports",        "/stock",      ],
      headline : "",
    }
  },
  methods:{
     getRandomInt:function(max) {
      return Math.floor(Math.random() * max);
      }
  },
  created() {
        var url = this.news[this.getRandomInt(6)];
        axios.get(url, {
          headers: {
            "Content-Type": "application/xml",
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept"
          },
        })
        .then(response => {
          const parser = new xmlParser.XMLParser();
          var xml = response.data.replaceAll("<BR>", "").replaceAll("<br>", "")
          var data = parser.parse(xml)
          data.rss.channel.item.forEach((item) => {
            this.headline += "• "  + item.description.replace(/<[^>]*>?/g, ''); + " "+ (new Date(item.pubDate)).toLocaleTimeString() + "     "
          })
        });



    this.load = true



  }


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.footer{

}
</style>
