<template>
  <div class="cntsTitle">
    &bull; 오늘의 날씨
  </div>
  <div class="weather">
    <div class="toDayWeather">
      <div class="toDayIcon">
        <img v-bind:src="toDayWeatherIcon" class="toDayImgIcon">
      </div>
      <div class="toDayTemp">
        {{ toDayWeather.temp }} &#8451;
      </div>
    </div>
    <div class="weekWeather">
      <template v-for="(item, i) in weekWeatherData.data" v-bind:key="i">
        <template v-if="i%2 != 0">
          <div class="weekItem">
              <div class="weekItemTitle" >
                {{ (new Date(item.tmEf)).getDate() }}일 ({{ days[(new Date(item.tmEf)).getDay()] }})
              </div>
              <div class="weekItemImg">
                <img v-bind:src="getImgUrl(weatherIconNum.get(item.wf))"  class="weekItemIcon">
              </div>
              <div class="weekItemTemp">
                {{ item.tmn }}&#8451;/{{ item.tmx }}&#8451;
              </div>
          </div>
        </template>
      </template>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import xmlParser from 'fast-xml-parser'
export default {
  name: 'WallBoardMain',
  components: {
  },
  props: {
    msg: String
  },
  data(){
    return{
      toDayWeather: "",
      toDayWeatherIcon:"",
      toDayWf:"",
      weekWeatherData: "",
      days : [
        '일요일',
        '월요일',
        '화요일',
        '수요일',
        '목요일',
        '금요일',
        '토요일'
      ],
      weatherIconNum : new Map([
        ["맑음", 1],
        ["구름조금", 2],
        ["구름많음",3],
        ["흐림",4 ],
        ["비",5],
        ["눈",6],
        ["비 또는 눈",7 ],
        ["눈 또는 비",8],
        ["낙뢰",9 ],
        ["연무",10],
        ["안개",11],
        ["황사",12 ],
        ["맑음2",13],
        ["구름조금2",14 ],
        ["구름많음2",15],
        ["소나기",16],
        ["가끔 비, 한때 비",17],
        ["가끔 눈, 한때 눈",18],
        ["빗방울",19],
        ["눈날림",20 ],
        ["박무 (엷은 안개)",21]
      ])

    }
  },
  methods:{
    getImgUrl:function(iconNumber) {
      // console.log(iconNumber)
      var images = require.context('../assets/', false, /\.png$/)
      return images('./' + iconNumber + ".png")
    }
  },
  async created () {

    axios.get('/wid', {
      headers: {
        "Content-Type": "application/xml",
        "Access-Control-Allow-Origin": "*", "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept"
      },
    })
    .then(response => {
      // console.log(response.data);
      const parser = new xmlParser.XMLParser();
      var data = parser.parse(response.data)
      this.toDayWeather = data.rss.channel.item.description.body.data[0]
      this.toDayWeatherIcon = this.getImgUrl(data.rss.channel.item.description.body.data[0].sky)
    });

    axios.get('/weather/forecast/mid-term-rss3.jsp?stnId=109', {
      headers: {
        "Content-Type": "application/xml",
        "Access-Control-Allow-Origin": "*", "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept"
      },
    })
    .then(response => {
      // console.log(response.data);
      const parser = new xmlParser.XMLParser();
      var data = parser.parse(response.data)
      this.weekWeatherData = data.rss.channel.item.description.body.location[0]
      // console.log(weatherIconNum.get("맑음"))

      // console.log(this.weekWeatherData.data)
    });
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cntsTitle{
  font-size: 17px;
  margin-left: 10px;
  text-align: left;
  /*border-bottom: 3px #888;*/
  /*border-bottom-style: double ;*/
  /*width: 100px;*/

}

.weather{
  /*border-bottom: 1px solid #ccc;*/
  height: 280px;

}

.weather .toDayWeather{
  height: 150px;
}

.weather .toDayWeather .toDayIcon{
  float: top;
}

.weather .toDayWeather .toDayIcon .toDayImgIcon{
  width: 80px;
  margin-top: 5px;
}



.weather .toDayWeather .toDayTemp{
  margin-top: 5px;
}


.weather .weekWeather{
  width:100%;
  position: relative;
}

.weather .weekWeather .weekItem{
  float: left;
  width: 16%;
  /*border: 1px solid #000;*/
}

.weather .weekWeather .weekItem .weekItemTitle{
  font-size:12px;
}

.weather .weekWeather .weekItem .weekItemImg{
}

.weather .weekWeather .weekItem .weekItemImg .weekItemIcon{
  margin-top: 10px;
  width: 25%;
}


.weather .weekWeather .weekItem .weekItemTemp{
  margin-top : 10px;
  font-size: 12px;
}

.weather .weekWeather:after{ clear: both; display: block; content: '' }


</style>
